pre[class*='language-'] {
  color: #f4f4f5;
}

.token.tag,
.token.class-name,
.token.selector,
.token.selector .class,
.token.selector.class,
.token.function {
  color: #60a5fa;
}

.token.attr-name,
.token.keyword,
.token.rule,
.token.pseudo-class,
.token.important {
  color: #cbd5e1;
}

.token.module {
  color: #34d399;
}

.token.attr-value,
.token.class,
.token.string,
.token.property {
  color: #5eead4;
}

.token.punctuation,
.token.attr-equals {
  color: #71717a;
}

.token.unit,
.language-css .token.function {
  color: #bae6fd;
}

.token.comment,
.token.operator,
.token.combinator {
  color: #a1a1aa;
}
